svg,
.icon {
    width: 1em;
    height: 1em;
    display: inline-block;
    color: inherit;
    fill: currentColor;
}

.img-cover {
    display: block;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.img-contain {
    display: block;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: contain;
    }
}

.color-blue {
    color: #4380FF !important;
}

.color-pink {
    color: #C79CFF !important;
}

.color-white {
    color: $color-white-1 !important;
}

.area-click {
    --indent: -5px;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: var(--indent);
        right: var(--indent);
        bottom: var(--indent);
        left: var(--indent);
    }
}

.link-cover {
    &:before {
        width: 100%;
        height: 100%;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
    }
}

.btn-reset {
    background: transparent;
    margin: 0;
    padding: 0;
    font-size: 0;
    outline: none;
    border: none;
    border-radius: 0;
}

.btn {
    background: transparent;
    max-width: 100%;
    padding: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: bold;
    line-height: 1.35;
    letter-spacing: 0;
    color: inherit;
    text-align: center;
    text-transform: none;
    border: none;
    border-radius: 0;
    outline: none;
    user-select: none;
    transition: all .4s;
    position: relative;
    cursor: pointer;
    z-index: 0;
    &:hover {
        text-decoration: none;
    }
    &.disabled,
    &:disabled {
        pointer-events: none;
    }
    &--theme-accent {
        background: $gradient-blue-1;
        font-weight: bold;
        color: $color-white-1;
        border-radius: 2rem;
        &:hover {
            @media (min-width: $md-mf) {
            }
            &:before {
                @media (min-width: $md-mf) {
                    visibility: visible;
                    opacity: 1;
                }
            }
        }
        &:before {
            background: rgba($color-black-1, .2);
            width: 100%;
            height: 100%;
            content: '';
            border-radius: inherit;
            position: absolute;
            top: 0;
            left: 0;
            transition: all .4s;
            visibility: hidden;
            opacity: 0;
            z-index: -1;
        }
    }
    &--theme-green {
        background: $color-green-3;
        font-weight: normal;
        color: $color-white-2;
        border-radius: 1.2rem;
        &:hover {
            @media (min-width: $md-mf) {
                background: #117E4A;
            }
        }
    }
    &--theme-access {
        background: $gradient-green-1;
        font-weight: bold;
        color: $color-white-1;
        border-radius: 2rem;
        &:hover {
            @media (min-width: $md-mf) {
            }
            &:before {
                @media (min-width: $md-mf) {
                    visibility: visible;
                    opacity: 1;
                }
            }
        }
        &:before {
            background: rgba($color-black-1, .2);
            width: 100%;
            height: 100%;
            content: '';
            border-radius: inherit;
            position: absolute;
            top: 0;
            left: 0;
            transition: all .4s;
            visibility: hidden;
            opacity: 0;
            z-index: -1;
        }
    }
    &--theme-accent-border {
        background: transparent;
        font-weight: bold;
        border-radius: 1.2rem;
        &:before,
        &:after {
            content: '';
            position: absolute;
            transition: all .4s;
        }
        &:before {
            background: $gradient-blue-1;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            border-radius: inherit;
            z-index: -2;
        }
        &:after {
            background: rgba($color-gray-1, .88);
            top: 2px;
            right: 2px;
            bottom: 2px;
            left: 2px;
            border-radius: 1rem;
            z-index: -1;
        }
        &:hover {
            @media (min-width: $lg-mf) {
            }
            &:after {
                @media (min-width: $lg-mf) {
                    background: rgba($color-gray-1, .85);
                    box-shadow: 0 0 1rem .2rem rgba($color-blue-1, .3);
                }
            }
        }
    }
    &--theme-blue {
        background: $color-blue-5;
        color: $color-white-1;
        border-radius: 1.7rem;
        &:hover {
            @media (min-width: $md-mf) {
                background: #1a5bdf;
            }
        }
    }
    &--size {
        &-xs {
            min-height: 3.2rem;
            padding: .8rem 1.2rem;
            font-size: 1.2rem;
        }
        &-sm {
            min-height: 5.2rem;
            padding: 1.2rem 4.0rem;
            font-size: 1.4rem;
        }
        &-md {
            min-height: 5.2rem;
            padding: 1.2rem 4.0rem;
            font-size: 1.5rem;
        }
    }
    &__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 1;
    }
    &__title {
        padding-top: .1em;
    }
    &--theme-accent-border &__title {
        background-color: $color-blue-1;
        background-image: $gradient-blue-1;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    &__icon {
        width: 1.5em;
        height: 1.5em;
        &--rtl {
            margin-right: .6em;
        }
    }
}

.u-input {
    background: transparent;
    width: 100%;
    display: block;
    font-style: normal;
    font-weight: normal;
    line-height: 1.35;
    text-align: left;
    border: none;
    border-radius: 0;
    outline: none;
    transition: all .4s;
    &--theme {
        &-gray {
            background: rgba($color-black-1, .08);
            border-radius: .8rem;
            &:hover {
                @media (min-width: $lg-mf) {
                    box-shadow: 0 0 0 2px $color-blue-1;
                }
            }
            &:focus {
                box-shadow: 0 0 0 2px $color-blue-1;
            }
            &::placeholder {
                color: $color-gray-2;
            }
        }
    }
    &--size {
        &-md {
            min-height: 4.4rem;
            padding: 1.2rem 1.6rem;
            font-size: 1.5rem;
        }
    }
    &::placeholder {
        transition: all .34s;
    }
    &:focus::placeholder {
        color: transparent;
        transform: translateY(-4rem);
    }
    &:hover,
    &:focus {
    }
    &[type=number] {
        -moz-appearance: textfield;
    }
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
        margin: 0;
        -webkit-appearance: none;
    }
}

.content {
    line-height: 1.52;
    & > :first-child {
        margin-top: 0;
    }
    & > :last-child {
        margin-bottom: 0;
    }
    p {
        margin: 0 0 1.4em;
    }
    a {
        color: $color-blue-1;
        box-shadow: 0 1px 0 transparent;
        transition: all .4s;
        &:hover {
            text-decoration: none;
            @media (min-width: $lg-mf) {
                box-shadow: 0 1px 0 currentColor;
            }
        }
    }
}

.access-list {
    margin: 0;
    padding: 0;
    color: $color-green-1;
    list-style-type: none;
    li {
        background: rgba($color-green-1, .16);
        margin: 0;
        padding: 1.1rem 4.0rem 1.1rem 5.0rem;
        border-radius: 2.0rem;
        position: relative;
        &:before {
            background: url(../images/icon/list-access-icon.svg) 50% no-repeat;
            width: 2.4rem;
            height: 2.4rem;
            content: '';
            position: absolute;
            top: .8rem;
            left: 1.6rem;
        }
    }
    li + li {
        margin-top: 1.2rem;
    }
}

.protect-aside {
    &__body {
        padding-bottom: 115.942028986%;
        position: relative;
        @media (max-width: $sm) {
            padding-bottom: 0;
        }
    }
    &__inner {
        width: 100%;
        height: 100%;
        padding: 2.4rem 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        border-radius: 2.4rem .8rem 2.4rem 2.4rem;
        position: absolute;
        top: 0;
        left: 0;
        @media (max-width: $sm) {
            padding: 2rem 1rem;
            flex-direction: row;
            position: relative;
        }
        &--theme {
            &-green {
                background: rgba(80,175,149, .20);
                color: #50AF95;
            }
            &-yellow {
                background: rgba($color-yellow-2, .2);
                color: $color-yellow-2;
            }
            &-blue {
                background: rgba($color-blue-4, .2);
                color: $color-blue-4;
            }
        }
    }
    &__icon {
        width: auto;
        height: 4rem;
        margin: 0 auto 2rem;
        display: block;
        user-select: none;
        @media (max-width: $sm) {
            margin: 0 2rem 0 0;
        }
    }
    &__title {
        margin: 0 0 .8rem;
        font-weight: 600;
        font-size: var(--fs-3);
        line-height: 1.05;
        text-transform: uppercase;
        @media (max-width: $sm) {
            margin-bottom: 0;
        }
        strong {
            font-size: var(--fs-3);
        }
    }
    &__subtitle {
        font-weight: 500;
        font-size: 1.2rem;
        color: rgba($color-white-1, .6);
        text-transform: uppercase;
    }
}

.u-radio {
    min-height: var(--radio-height);
    padding: var(--radio-padding-y) var(--radio-padding-x);
    display: flex;
    align-items: center;
    border-radius: var(--radio-radius);
    cursor: pointer;
    &--theme {
    }
    &--size {
        &-md {
            --radio-height: 5.6rem;
            --radio-padding-x: 1.6rem;
            --radio-padding-y: 1.6rem;
            --radio-icon-size: 16px;
            --radio-icon-mr: .8rem;
            --radio-img-height: 2.4rem;
            --radio-radius: 1.2rem;
        }
    }
    &__control {
        position: absolute;
        left: -9999px;
    }
    &__icon {
        width: var(--radio-icon-size);
        height: var(--radio-icon-size);
        margin-right: .8rem;
        display: block;
        flex-shrink: 0;
        border-radius: 50%;
        border: 2px solid currentColor;
        transition: all .22s linear;
    }
    &--theme-gray &__control:checked ~ &__icon {
        background: currentColor;
    }
    &--theme-gray:hover &__control:not(:checked) ~ &__icon {
        @media (min-width: $lg-mf) {
            background: currentColor;
        }
    }
    &__img {
        height: var(--radio-img-height);
        margin: 0 0 0 auto;
        display: block;
    }
    &--visa {
        background: rgba($color-blue-4, .2);
        color: $color-blue-4;
    }
    &--bitcoin {
        background: rgba(#E39600, .2);
        color: #E39600;
    }
    &--qiwi {
        background: rgba(#FF8C00, .2);
        color: #FF8C00;
    }
    &--usdt {
        background: rgba(#2D9075, .2);
        color: #2D9075;
    }
    &__title {
        font-size: 1.5rem;
        color: #181829;
    }
}

.u-input-area {
    width: 100%;
    min-height: var(--input-area-height);
    padding: var(--input-area-padding-y) var(--input-area-padding-x);
    display: block;
    font-size: var(--input-area-font-size);
    line-height: 1;
    text-align: left;
    border-radius: var(--input-area-radius);
    white-space: nowrap;
    text-overflow: ellipsis;
    transition: all .4s;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    @extend .btn-reset;
    &--theme {
        &-gray {
            background: rgba($color-black-1, .08);
            color: $color-black-1;
            &:hover {
                @media (min-width: $md-mf) {
                    background: rgba($color-black-1, .18);
                }
            }
        }
    }
    &--size {
        &-sm {
            --input-area-height: 5.6rem;
            --input-area-padding-x: 1.6rem;
            --input-area-padding-y: .7rem;
            --input-area-radius: .8rem;
            --input-area-font-size: 1.5rem;
        }
    }
    &:after {
        background: $color-green-3;
        width: 100%;
        height: 100%;
        padding: inherit;
        padding-bottom: .55em;
        content: attr(data-copy-text);
        font-size: 1.6rem;
        color: $color-white-1;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        transition: all .4s;
    }
    &:not(.is-copied) {
        &:after {
            opacity: 0;
            visibility: hidden;
        }
    }
}

.dropdown-block {
    background: rgba(255,177,27, .24);
    border-radius: 1.6rem;
    &--theme {
        &-baidge {
            background: rgba(255,177,27, .24);
            border-radius: 1.6rem;
        }
    }
    &__inner {
        padding: 1.6rem;
    }
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-weight: 600;
        font-size: 1.2rem;
        color: $color-brown-1;
        position: relative;
        &-title {
            transition: all .3s;
            transition-property: color, opacity;
        }
        &-btn {
            @extend .btn-reset, .link-cover;
            padding: .6rem;
            display: inline-flex;
            align-items: center;
            font-weight: inherit;
            font-size: 1.2rem;
            color: #7C4D00;
            flex-shrink: 0;
            cursor: pointer;
            &-title {
                padding-top: .1em;
                &:before {
                    content: attr(data-title-normal);
                    display: block;
                }
                &:after {
                    content: attr(data-title-active);
                    display: block;
                }
            }
            &-icon {
                width: 1.2rem;
                height: 1.2rem;
                margin-left: .4rem;
                flex: 0 0 1.2rem;
                transition: all .4s;
            }
        }
    }
    &:not(.is-active) &__header-btn-icon {
        transform: rotate(180deg);
    }
    &.is-active &__header-btn-title:before,
    &:not(.is-active) &__header-btn-title:after {
        display: none;
    }
    &.is-active &__header-title {
        color: $color-black-2;
        opacity: .5;
    }
    &__content {
        margin-top: 1.2rem;
        position: relative;
    }
    &:not(.is-active) &__content {
        display: none;
    }
}

.line-info {
    display: flex;
    align-items: flex-start;
    @media (max-width: $sm) {
        flex-wrap: wrap;
    }
    &__title {
        max-width: 16rem;
        min-height: 3.2rem;
        margin-right: 1.2rem;
        padding-top: .1em;
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex: 0 0 16rem;
        font-weight: 600;
        font-size: 1.2rem;
        text-transform: uppercase;
        opacity: .5;
        @media (max-width: $sm) {
            max-width: 100%;
            min-height: 0;
            margin: 0 0 1rem;
            flex-basis: 100%;
        }
    }
    &__body {
        flex-grow: 1;
        @media (max-width: $sm) {
            flex-wrap: wrap;
        }
    }
    &__list {
        margin: -.6rem 0 -.6rem -.6rem;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        @media (max-width: $sm) {
            flex-basis: 100%;
        }
        &-item {
            margin: .6rem;
        }
    }
    &__btn-download {
        margin-left: auto;
        flex-shrink: 0;
        @media (max-width: $sm) {
            margin: 2.0rem 0 0;
        }
    }
    &__similar-name {
        margin-top: 1.2rem;
    }
}

.info-badge {
    padding: .9rem 1rem;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    line-height: 1;
    border-radius: 1.2rem;
    &--theme {
        &-fill {
            background: rgba($color-black-1, .08);
        }
        &-outline {
            box-shadow: inset 0 0 0 1px rgba($color-black-1, .08);
        }
        &-instagram {
            background: radial-gradient(99.11% 99.11% at 26.56% 107.70%, rgba(255, 221, 85, 0.32) 0%, rgba(255, 221, 85, 0.32) 10.00%, rgba(255, 84, 62, 0.32) 50%, rgba(200, 55, 171, 0.32) 100%);
        }
        &-telegram {
            background: linear-gradient(180deg, rgba(42, 171, 238, 0.32) 0%, rgba(34, 158, 217, 0.32) 100%);
        }
        &-youtube {
            background: rgba(244,54,53, .32);
        }
        &-gray-light {
            background: rgba($color-black-1, .08);
            color: rgba($color-black-2, .7);
        }
        &-green {
            background: linear-gradient(90deg, rgba(55,198,171, .12) 0%, rgba(55,198,171, 0) 100%);
            color: #37C6AB;
        }
    }
    &--size {
        &-md {
            padding: 1.1rem 1.2rem;
            font-size: 1.4rem;
        }
    }
    &__icon {
        width: 2.4rem;
        height: 2.4rem;
        margin: -1rem .8rem -.9rem -.6rem;
        flex-shrink: 0;
    }
    &__title {
        font-weight: 600;
        padding-top: .1em;
    }
    &__suptitle {
        margin-right: .7ch;
        padding-top: .1em;
    }
    &__subtitle {
        background: rgba($color-black-1, .08);
        margin-left: .7ch;
        padding: .2rem .4rem;
        font-weight: 600;
        font-size: 66.6667%;
        color: rgba($color-black-1, .5);
        border-radius: .4rem;
    }
}

.media-card {
    padding-bottom: 100%;
    border-radius: 1.2rem;
    pointer-events: none;
    user-select: none;
    @extend .img-cover;
    &--blur {
        position: relative;
        &:before {
            background: rgba($color-black-1, .12);
            backdrop-filter: blur(.8rem);
            width: 100%;
            height: 100%;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            border-radius: inherit;
            z-index: 1;
        }
    }
}

.u-progress {
    &__line {
        background: rgba($color-blue-1, .8);
        width: 100%;
        min-height: 5.2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 600;
        font-style: italic;
        font-size: 1.6rem;
        color: $color-white-1;
        text-align: center;
        border-radius: 1.2rem;
        position: relative;
        pointer-events: none;
        cursor: default;
        overflow: hidden;
        z-index: 0;
        &-current {
            background: $color-blue-1;
            width: 0;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            transition: ease 1s .6s;
            z-index: -1;
            &.is-active {
                width: 100%;
            }
        }
    }
}

.u-file-el {
    display: none;
    position: absolute;
    left: -9999px;
}

.logo {
    width: 12.0rem;
    height: 8.0rem;
    display: block;
    flex-shrink: 0;
    transition: all .4s;
    &:hover {
        text-decoration: none;
        @media (min-width: $md-mf) {
            opacity: .8;
        }
    }
    &--mobile {
        width: 17rem;
        height: auto;
    }
    &--footer {
        width: 16.6rem;
        height: auto;
    }
    &__img {
        width: 100%;
        height: 100%;
        display: block;
        object-fit: contain;
    }
}

.course-card {
    color: $color-white-1;
    border-radius: 1.2rem;
    position: relative;
    overflow: hidden;
    z-index: 0;
    &__picture {
        @extend .img-cover;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
    }
    &__inner {
        padding: 3.2rem 3.2rem 3.2rem 9.8rem;
        position: relative;
        z-index: 1;
        @media (max-width: $sm) {
            padding: 3.2rem 2.4rem;
        }
    }
    &__gift {
        background: linear-gradient(90deg,#5C00D4 0%,rgba(92,0,212, 0) 100%);
        margin: 0 -3.2rem 0 0;
        padding: .8rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-radius: 1.2rem 0 0 1.2rem;
        flex-shrink: 0;
        @media (max-width: $sm) {
            margin-right: .8rem;
            flex-grow: 1;
            justify-content: flex-start;
            white-space: auto;
        }
        &-title {
            margin: 0 1.2rem 0 .4rem;
            font-weight: bold;
            font-size: 1rem;
            line-height: 1.5;
            color: $color-white-1;
            text-transform: uppercase;
        }
        &-icon {
            width: 5.6rem;
            height: auto;
            margin: 0 3.2rem 0 0;
            flex-shrink: 0;
            @media (max-width: $sm) {
                max-width: 3.6rem;
                margin: 0 .8rem 0 0;
                order: -1;
            }
        }
    }
    &__header {
        margin: 0 0 4rem;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        @media (max-width: $sm) {
            margin-bottom: 0;
            flex-wrap: wrap;
        }
        &-content {
            max-width: 42rem;
            padding-right: 2rem;
            @media (max-width: $sm) {
                width: 100%;
                max-width: 100%;
                margin: 0 0 4rem;
                padding-right: 0;
                flex-basis: 100%;
                text-align: center;
            }
            &-title {
                margin: 0 0 .8rem;
                font-weight: bold;
                position: relative;
                &:before {
                    background: url(../images/icon/guard-type-3.svg) 50% no-repeat;
                    background-size: contain;
                    width: 3.4rem;
                    height: 4.2rem;
                    margin-right: 2.4rem;
                    content: '';
