:root {
    --fs-default: 1.4rem;
    --fs-1: 3.2rem;
    --fs-2: 2.4rem;
    --fs-3: 2.0rem;
    --fs-4: 1.8rem;
    --fs-5: 1.6rem;
    --fs-6: 1.4rem;

    --gap: 2.0rem;
    --gap-: -2.0rem;
    --gap-fluid: 4.0rem;
    --gap-fluid-: -4.0rem;
    @media (max-width: $md) {
        --fs-1: 2.4rem;
    }
}