.aside {
    transition: all 1.2s;
    transition-property: color;
    &__logo {
        margin: 0 auto 7rem;
        display: block;
    }
    &__inner {
        background: rgba(255,255,255, .72);
        backdrop-filter: blur(.6rem);
        padding: 3.2rem;
        border-radius: 1.6rem;
        position: relative;
        & > :last-child {
            margin-bottom: 0;
        }
    }
    &__title {
        margin: 0 0 1.2rem;
    }
    &__text {
        margin: 0 0 2rem;
        font-size: 1.2rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__form {
        &-input {
        }
        &-btn {
            width: 100%;
            margin: 1.2rem 0;
        }
    }
    &__footer {
        margin: 0;
        font-size: 1rem;
        color: $color-gray-2;
    }
    &__faq {
        background: rgba(0,59,181, .14);
        margin: 0 0 4rem;
        display: flex;
        align-items: center;
        border-radius: .8rem;
        transition: all .3s;
        transition-property: background;
        cursor: pointer;
        &:hover {
            @media (min-width: $lg-mf) {
                background: rgba(0,59,181, .2);
            }
        }
        &-picture {
            max-width: 5.2rem;
            margin: -.8rem .6rem -.8rem .2rem;
            margin-right: .6rem;
            flex: 0 0 5.2rem;
            img {
                width: 100%;
                height: auto;
                display: block;
            }
        }
        &-text {
            font-weight: 600;
            font-size: 1.2rem;
            color: $color-blue-1;
            border-bottom: 1px dashed currentColor;
        }
    }
    &__form-desc {
        margin: 0 0 1.2rem;
        font-weight: 600;
        font-size: 1.28rem;
        letter-spacing: -.01em;
        span {
            color: $color-green-4;
        }
    }
    &__slide {
        display: none;
    }
    &__check {
        text-align: center;
        .aside__title {
            margin-bottom: 3.2rem;
        }
    }
    &__progress {
        text-align: center;
        .aside__title {
            margin-bottom: 3.2rem;
        }
    }
}

.layout {
    background: rgba($color-gray-1, .88);
    backdrop-filter: blur(.6rem);
    border-radius: 2.4rem;
    &__slide {
        display: none;
        &-payment-method {
            display: none;
        }
    }
    &__result {
        margin: 0 0 3.2rem;
        @media (max-width: $sm) {
            margin-bottom: 3.2rem;
        }
        &-content {
            padding: 4.0rem;
            & > :last-child {
                margin-bottom: 0;
            }
            @media (max-width: $sm) {
                padding: 2rem;
            }
            &-header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                @media (max-width: $md) {
                    flex-wrap: wrap;
                }
                &-title {
                    margin: 0 1rem 4rem 0;
                    font-weight: bold;
                    @media (max-width: $md) {
                        margin: 0 0 1.2rem;
                    }
                }
                &-activity {
                    background: #0E9C69;
                    max-width: 100%;
                    margin: 0 0 4rem;
                    padding: .8rem 1.6rem .7rem;
                    font-size: 1.2rem;
                    color: $color-white-1;
                    border-radius: 1.6rem;
                    flex-shrink: 0;
                    @media (max-width: $md) {
                        margin: 0 0 2rem;
                    }
                    strong {
                        font-weight: 600;
                    }
                }
            }
            &-title {
                margin: 0 0 4.0rem;
                font-weight: bold;
                @media (max-width: $sm) {
                    margin-bottom: 2.0rem;
                }
            }
            &-list {
                margin: 0 0 4.0rem;
                @media (max-width: $sm) {
                    margin-bottom: 2.0rem;
                }
                &-line {
                    & + & {
                        margin-top: 2.4rem;
                        @media (max-width: $md) {
                            margin-top: 2rem;
                        }
                    }
                }
            }
            &-media {
                background: $color-white-1;
                margin: 0 0 4rem;
                padding: 3.2rem;
                border-radius: 1.6rem;
                @media (max-width: $sm) {
                    margin-bottom: 2rem;
                    padding: 1.6rem;
                }
                &-row {
                    margin: -.4rem;
                    display: flex;
                    flex-wrap: wrap;
                    &-col {
                        max-width: 10%;
                        padding: .4rem;
                        flex: 0 0 10%;
                        @media (max-width: $sm) {
                            max-width: 20%;
                            flex-basis: 20%;
                        }
                    }
                }
                &-header {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    @media (max-width: $md) {
                        flex-wrap: wrap;
                    }
                    &-el {
                        margin: 0 0 2.4rem;
                        display: flex;
                        align-items: center;
                        @media (max-width: $md) {
                            max-width: 100%;
                            margin: 0;
                            flex-basis: 100%;
                            flex-wrap: wrap;
                        }
                        &-title {
                            margin: 0 5.6rem 0 0;
                            flex-shrink: 0;
                            font-weight: bold;
                            @media (max-width: $md) {
                                margin-bottom: 1.2rem;
                            }
                        }
                        &-list {
                            @media (max-width: $md) {
                                margin-bottom: 1.2rem;
                            }
                        }
                    }
                    &-btn-download {
                        margin: 0 0 2.4rem;
                        flex-shrink: 0;
                        @media (max-width: $md) {
                            margin: .8rem 0 2.4rem;
                        }
                    }
                }
            }
            &-access-password {
                background: rgba($color-yellow-1, .24);
                padding: 1.2rem;
                display: block;
                font-weight: 600;
                font-size: 1.2rem;
                color: $color-brown-1;
                text-align: center;
                border-radius: .8rem;
            }
        }
        &-footer {
            background: rgba($color-green-1, .2);
            padding: 4.0rem;
            @media (max-width: $sm) {
                padding: 2.4rem;
            }
            &-title {
                margin: 0 0 1.2rem;
            }
            &-text {
                margin: 0 0 2.8rem;
            }
            &-btn {
                @media (max-width: $sm) {
                    width: 100%;
                    padding-right: 1.5rem;
                    padding-left: 1.5rem;
                    font-size: 1.4rem;
                }
            }
        }
    }
    &__protect {
        width: 100%;
        max-width: 80rem;
        margin: 0 auto;
        padding: 0 0 2rem;
        display: none;
        position: relative;
        @media (max-width: $md) {
            margin: 0 auto;
        }
        &-title {
            margin: 0 0 2.4rem;
            padding-bottom: 2.4rem;
            font-weight: bold;
            border-bottom: 1px solid rgba(#282828, .12);
            @media (max-width: $sm) {
            }
        }
        &-body {
            margin: 0 0 2.4rem;
            display: flex;
            justify-content: space-between;
            @media (max-width: $sm) {
                margin-bottom: 2.0rem;
                flex-wrap: wrap;
            }
            &-list {
                flex-grow: 1;
                @media (max-width: $sm) {
                    max-width: 100%;
                    flex-basis: 100%;
                }
            }
            &-aside {
                margin-left: 3.2rem;
                flex: 0 0 13.8rem;
                @media (max-width: $sm) {
                    margin: 2.0rem 0 0;
                    flex-basis: 100%;
                }
                .protect-aside__body {
                    @media (max-width: $sm) {
                        height: 14.0rem;
                        padding-bottom: 0;
                    }
                }
            }
        }
        &-payments {
            display: flex;
            justify-content: space-between;
            @media (max-width: $sm) {
                flex-wrap: wrap;
            }
            &-content {
                margin-right: 2.4rem;
                padding-right: 2.4rem;
                border-right: 1px solid rgba(#282828, .12);
                flex-grow: 1;
                @media (max-width: $sm) {
                    max-width: 100%;
                    margin: 0 0 2.4rem;
                    padding: 0 0 2.4rem;
                    flex-basis: 100%;
                    border-right: none;
                    border-bottom: 1px solid rgba(#282828, .12);
                }
            }
            &-title {
                margin: 0 0 1.2rem;
            }
            &-list {
                margin: -.6rem;
                display: flex;
                flex-wrap: wrap;
                @media (max-width: $sm) {
                    justify-content: center;
                    flex-wrap: wrap;
                }
                &-card {
                    max-width: 50%;
                    padding: .6rem;
                    flex: 0 0 50%;
                    @media (max-width: $xs) {
                        max-width: 100%;
                        flex-basis: 100%;
                    }
                }
            }
            &-btn {
                width: 100%;
                min-height: 5.6rem;
                padding-right: 1.0rem;
                padding-left: 1.0rem;
                flex-grow: 1;
                flex-shrink: 0;
                font-size: 1.5rem;
            }
            &-aside {
                min-width: 20rem;
                margin-left: auto;
                flex-shrink: 0;
                @media (max-width: $sm) {
                    width: 100%;
                    margin: 0;
                }
                &-header {
                    margin: 0 0 1.2rem;
                    padding: 1rem 0;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    @media (max-width: $sm) {
                        padding: 0;
                    }
                    &-title {
                        margin: 0 1.2rem 0 0;
                        font-weight: 600;
                        font-size: 1.4rem;
                        color: $color-gray-2;
                    }
                    &-amount {
                        font-weight: 600;
                        font-size: var(--fs-2);
                    }
                }
            }
        }
        &-block {
            background: rgba(255,255,255, .84);
            padding: 4rem;
            backdrop-filter: blur(4px);
            border-radius: 2.4rem;
            @media (max-width: $md) {
                margin: 0 2.6rem;
            }
            @media (max-width: $sm) {
                padding: 2.4rem;
            }
        }
    }
    &__transfer {
        background: rgba($color-white-1, .84);
        backdrop-filter: blur(4px);
        padding: 4rem;
        display: flex;
        align-items: flex-start;
        border-radius: 2rem;
        @media (max-width: $sm) {
            padding: 2.0rem;
            flex-wrap: wrap;
        }
        &-aside {
            margin-right: 3.2rem;
            flex: 0 0 13.8rem;
            @media (max-width: $sm) {
                margin: 0 0 2.0rem;
                flex-basis: 100%;
            }
            .protect-aside__body {
                @media (max-width: $sm) {
                    margin-bottom: 1.2rem;
                    padding-bottom: 0;
                }
            }
        }
        &-content {
            flex-grow: 1;
            overflow: hidden;
        }
        &-title {
            margin: 0 0 .8rem;
            font-weight: bold;
            font-size: var(--fs-2);
        }
        &-text {
            max-width: 43rem;
            margin: 0 0 2.2rem;
            font-size: 1.4rem;
            color: #6C6F71;
            @media (max-width: $sm) {
                max-width: 100%;
            }
        }
        &-group {
            display: flex;
            justify-content: space-between;
            @media (max-width: $xs) {
                flex-wrap: wrap;
            }
            &-input {
                margin-right: 2.0rem;
                flex-grow: 1;
                @media (max-width: $xs) {
                    max-width: 100%;
                    margin: 0 0 2.0rem;
                    flex-basis: 100%;
                }
            }
            &-btn {
                padding-right: 4.8rem;
                padding-left: 4.8rem;
                flex-shrink: 0;
                @media (max-width: $xs) {
                    max-width: 100%;
                    flex-basis: 100%;
                }
            }
        }
    }
}

body.is-payment .main__header-aside-identification {
    display: inline-flex;
}

.main {
    background: url(../images/bg-gradient.jpg) center top no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1;
    &:before {
        background: url(../images/bg-result-gradient.jpg) center top no-repeat;
        background-size: cover;
        background-attachment: fixed;
        width: 100%;
        height: 100%;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        visibility: hidden;
        transition: all .4s;
        z-index: -2;
    }
    &:after {
        background: url(../images/bg-result-completed.jpg) center top no-repeat;
        background-size: cover;
        background-attachment: fixed;
        width: 100%;
        height: 100%;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        visibility: hidden;
        transition: all .4s;
        z-index: -1;
    }
    &.is-completed:after {
        opacity: 1;
        visibility: visible;
    }
    &.main--bg-dark:before {
        opacity: 1;
        visibility: visible;
    }
    &__attention {
        background: url(../images/attention.png) 50% no-repeat;
        background-size: contain;
        width: 14.9rem;
        height: 14.9rem;
        position: absolute;
        top: 52rem;
        left: 10rem;
        opacity: 0;
        visibility: hidden;
        transition: all 1s;
        @media (max-width: $md) {
            display: none !important;
        }
    }
    &__header {
        width: 100%;
        max-width: 118.0rem;
        margin: 0 auto;
        padding: 0 var(--gap-fluid);
        display: flex;
        align-items: flex-start;
        position: relative;
        z-index: 1;
        @media (max-width: $sm) {
            padding: 0 2.6rem;
            flex-wrap: wrap;
        }
        &-aside {
            max-width: 32rem;
            padding: var(--gap-fluid) 0;
            flex: 0 0 32rem;
            position: relative;
            @media (min-width: $sm-mf) {
                margin-right: var(--gap-fluid);
            }
            @media (max-width: $sm) {
                margin: 0 auto;
                padding: 3.2rem 0;
            }
            &-identification {
                background: rgba($color-white-1, .16);
                height: 44px;
                padding: 1.6rem;
                display: none;
                align-items: center;
                color: $color-white-1;
                border-radius: 3rem;
                position: absolute;
                top: -10.4rem;
                left: 0;
                transition: all .4s;
                white-space: nowrap;
                @media (max-width: $md) {
                    display: none !important;
                }
                &:hover {
                    text-decoration: none;
                    @media (min-width: $md-mf) {
                        background: rgba($color-white-1, .3);
                    }
                }
                svg {
                    width: 2.2rem;
                    height: 2.2rem;
                    margin: -.2rem .6rem 0 0;
                    flex-shrink: 0;
                    transform: rotate(90deg);
                }
                span {
                    margin-right: .8rem;
                    font-weight: 600;
                    font-size: 1.4rem;
                    line-height: 1;
                }
            }
        }
        &-logo {
            margin: 0 auto;
            display: block;
            @media (max-width: $sm) {
                width: 19rem;
                height: auto;
            }
        }
        &-content {
            flex-grow: 1;
            @media (max-width: $sm) {
                width: 100%;
                order: -1;
            }
            &-security {
                background: rgba($color-green-3, .88);
                min-width: 10rem;
                padding: .8rem 3.6rem;
                display: inline-flex;
                justify-content: center;
                align-items: center;
                font-size: 1.4rem;
                color: $color-white-1;
                border-radius: 0 0 1.6rem 1.6rem;
                @media (max-width: $sm) {
                    width: 100%;
                }
                @media (min-width: $sm-mf) {
                    position: absolute;
                    top: 0;
                    left: 50%;
                    transform: translateX(-50%);
                }
                &.is-active {
                    @media (min-width: $sm-mf) {
                        min-width: 100%;
                        margin-left: calc(-1 * var(--gap-fluid));
                        position: relative;
                        left: 0;
                        transform: translateX(var(--gap-fluid));
                    }
                }
                img {
                    max-width: 2.4rem;
                    height: auto;
                    margin-right: 1.2rem;
                    flex-shrink: 0;
                }
                span {
                }
            }
        }
    }
    &__inner {
        width: 100%;
        max-width: 118.0rem;
        margin: 0 auto;
        padding: 0 var(--gap-fluid) var(--gap);
        display: flex;
        align-items: flex-start;
        transition: all .4s;
        @media (max-width: $sm) {
            padding: 0 2.6rem 5rem;
            flex-wrap: wrap;
        }
    }
    &__aside {
        max-width: 32rem;
        margin-right: var(--gap-fluid);
        flex: 0 0 32rem;
        position: relative;
        transition: all .7s;
        transition-delay: .5s;
        z-index: 1;
        @media (max-width: $sm) {
            max-width: 100%;
            margin: 0;
            flex-basis: 100%;
        }
        @media (min-width: $sm-mf) {
            position: sticky;
            top: var(--gap-fluid);
        }
        &.is-faq-active {
            @media (max-width: $sm) {
                display: none;
            }
        }
    }
    &__aside.is-hide,
    &__layout.is-hide {
        transition-duration: 1.4s;
        transition-delay: 0s;
        opacity: 0;
        visibility: hidden;
        position: absolute;
    }
    &__layout {
        flex-grow: 1;
        transition: all 1.1s ease;
        transition-delay: .64s;
        @media (max-width: $lg) {
            max-width: 100%;
            flex-basis: 100%;
        }
        &:not(.is-show) {
            transform: translateX(-60vw);
            opacity: 0;
            visibility: hidden;
            position: absolute;
        }
        &.is-hide {
            transform: translateX(80vw);
        }
    }
    &__payments {
        display: none;
        &.main__transfer-wrap {
            @media (max-width: $md) {
                margin: 0 2.6rem;
            }
        }
        &-payment-method {
        }
    }
    &__success {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        &:not(.is-show) {
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
        }
        &-content {
            background: rgba(#389A41, .88);
            backdrop-filter: blur(.6rem);
            width: 100%;
            max-width: 32rem;
            margin-top: 6rem;
            padding: 4.8rem 3.2rem;
            color: $color-white-1;
            text-align: center;
            position: absolute;
            top: 50%;
            left: 50%;
            border-radius: 2rem;
            box-shadow: 0 .8rem .24rem #3D97453D;
            box-shadow: 0 .4rem .8rem #3D974529;
            transform: translate(-50%, -50%);
            transition: all 1.4s;
            transition-delay: 1.6s;
            @media (max-width: $sm) {
                padding: 2.4rem;
            }
            @media (max-width: 38.0rem) {
                max-width: calc(100vw - 2 * var(--gap));
                min-height: 22.0rem;
            }
        }
        &:not(.is-show) &-content {
            transform: translate(-50%, -50%) scale(0);
            opacity: 0;
            visibility: hidden;
        }
        &-icon {
            width: auto;
            height: 3.4rem;
            margin: 0 auto 2.4rem;
            display: block;
            user-select: none;
            @media (max-width: $sm) {
                margin-bottom: 2.0rem;
            }
        }
        &-text {
            margin: 0;
            font-weight: bold;
            font-size: var(--fs-3);
            line-height: 1.32;
            @media (max-width: $sm) {
                font-size: 1.6rem;
            }
        }
        &-desc {
            margin-top: .8rem;
            line-height: 1.52;
        }
    }
    &__result {
        width: 100%;
        max-width: 80rem;
        margin: 0 auto;
        padding: 0 0 2rem;
        display: none;
        &-faq {
            margin: 0 0 3.2rem;
            @media (max-width: $md) {
                margin-right: 2.6rem;
                margin-left: 2.6rem;
            }
        }
        &-inner {
            position: relative;
        }
        &-layout {
            @media (max-width: $md) {
                margin-right: 2.6rem;
                margin-left: 2.6rem;
            }
        }
        &-logo {
            margin: 0 auto 5.77777777778rem;
            display: block;
            @media (max-width: $md) {
                margin: 0 auto 3.2rem var(--gap);
            }
        }
        &-error {
            background: #FA4549;
            margin: 0 0 3.2rem;
            padding: 2.4rem 3.2rem;
            display: flex;
            align-items: flex-start;
            color: $color-white-1;
            border-radius: 2rem;
            box-shadow: 0 .8rem 2.4rem rgba(178,46,36, .24), 0 .4rem .8rem rgba(238,83,71, .16);
            @media (max-width: $md) {
                margin: 0 2.6rem 3.2rem;
            }
            @media (max-width: $sm) {
                padding: 2.4rem;
            }
            &-icon {
                width: 2.8rem;
                height: auto;
                margin-right: 1.2rem;
                flex-shrink: 0;
            }
            &-content {
            }
            &-title {
                min-height: 2.4rem;
                display: flex;
                flex-direction: column;
                justify-content: center;
                margin: 0 0 .8rem;
                @media (max-width: $sm) {
                    font-size: 2rem;
                }
            }
            &-text {
                font-size: 1.4rem;
                @media (max-width: $sm) {
                    font-size: 1.2rem;
                }
            }
        }
        &-user {
            margin: 0 0 3.2rem;
            display: flex;
            align-items: center;
            @media (max-width: $md) {
                margin-bottom: 3.2rem;
                padding: 0 2.6rem;
            }
            @media (max-width: $sm) {
                flex-wrap: wrap;
            }
            @media (max-width: $xs) {
                margin-bottom: 2.2rem;
            }
            &-avatar {
                @extend .img-cover;
                width: 4.8rem;
                height: 4.8rem;
                margin-right: 2.4rem;
                box-shadow: 0 0 0 .4rem rgba(255,255,255, .16);
                border-radius: 50%;
                overflow: hidden;
                @media (max-width: $sm) {
                    margin: 0 auto 2rem 0;
                    order: -1;
                }
            }
            &-nav {
                @media (max-width: $xs) {
                    max-width: 100%;
                    margin-bottom: 1rem;
                    flex-basis: 100%;
                }
                ul {
                    background: rgba(24,24,41, .16);
                    margin: 0;
                    padding: 0 2.4rem;
                    display: flex;
                    list-style-type: none;
                    border-radius: 2.6rem;
                    @media (max-width: $sm) {
                        padding-right: 1rem;
                        padding-left: 1rem;
                        justify-content: center;
                    }
                }
                li {
                    margin: 0;
                    padding: 0;
                    &:not(:last-child) {
                        margin-right: 2rem;
                        @media (max-width: $xs) {
                            margin-right: 3vw;
                        }
                    }
                }
                a {
                    padding: 1.4rem 0;
                    display: block;
                    font-weight: 600;
                    font-size: 1.6rem;
                    color: rgba($color-white-1, .5);
                    transition: all .4s;
