        }
        &-layout {
        }
        &-header {
            margin: 0 0 3.2rem;
            display: flex;
            align-items: center;
            color: $color-white-1;
            @media (max-width: $md) {
                margin-bottom: 2rem;
            }
            @media (max-width: $md) {
                margin-bottom: 3.2rem;
            }
            @media (max-width: $sm) {
            }
            &-icon {
                width: 3.4rem;
                height: auto;
                margin-right: 2.4rem;
                display: block;
                flex-shrink: 0;
            }
            &-sup {
                &-title {
                    margin: 0;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    line-height: 1.3;
                    @media (max-width: $sm) {
                        min-height: 0;
                    }
                }
                &-subtitle {
                    margin: 0;
                    font-size: 1.4rem;
                    line-height: 1.52;
                }
            }
        }
        &-course-card {
            margin-bottom: 3.2rem;
            @media (max-width: $md) {
                margin: 0 2.6rem 3.2rem;
            }
        }
        &-appeal {
            margin: 0 0 3.2rem;
            counter-reset: appeal-result-counter;
            @media (max-width: $md) {
                margin-left: var(--gap);
                margin-right: var(--gap);
            }
            &-item {
                background: rgba($color-white-1, .84);
                padding: 1.6rem;
                font-size: 1.6rem;
                counter-increment: appeal-result-counter;
                display: flex;
                align-items: flex-start;
                color: #181829;
                @media (max-width: $sm) {
                    font-size: 1.4rem;
                }
                &:before {
                    min-width: 2.8rem;
                    padding-right: .5rem;
                    content: counter(appeal-result-counter);
                    color: #696969;
                }
                &:first-child {
                    border-top-left-radius: 1.2rem;
                    border-top-right-radius: 1.2rem;
                }
                &:last-child {
                    border-bottom-left-radius: 1.2rem;
                    border-bottom-right-radius: 1.2rem;
                }
                &:not(:first-child) {
                    margin-top: 2px;
                }
            }
        }
        &-footer {
            background: rgba($color-black-3, .88);
            padding: 3.2rem;
            border-radius: 2rem;
            overflow: hidden;
            position: relative;
            z-index: 0;
            @media (max-width: $md) {
                margin: 0 2.6rem;
                padding-right: 2.4rem;
                padding-left: 2.4rem;
            }
            &:before {
                background: radial-gradient(50% 50.00% at 50% 50.00%, rgba(55, 198, 171, 0.78) 0%, rgba(55, 198, 171, 0.00) 100%);
                width: 32rem;
                height: 32rem;
                margin-top: -18rem;
                content: '';
                border-radius: 50%;
                opacity: .32;
                position: absolute;
                top: 50%;
                left: -16rem;
                z-index: -1;
            }
            &-inner {
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                @media (max-width: $md) {
                    flex-wrap: wrap;
                }
            }
            &-list {
                margin-right: 5.2rem;
                @media (max-width: $md) {
                    width: 100%;
                    margin: 0 0 5.2rem;
                }
                &-item {
                    padding-top: .6rem;
                    padding-bottom: .6rem;
                    line-height: 1.52;
                    &:not(:first-child) {
                        margin-top: .8rem;
                    }
                    .info-badge__icon {
                        margin-left: 0;
                    }
                }
            }
            &-price {
                flex-shrink: 0;
                @media (max-width: $md) {
                    max-width: 100%;
                    flex-basis: 100%;
                }
                &-card {
                    padding: 1.6rem;
                    display: flex;
                    justify-content: space-between;
                    border-radius: 1.2rem;
                    white-space: nowrap;
                    @media (max-width: $xs) {
                        padding: 1.4rem;
                    }
                    &:not(:last-child) {
                        margin-bottom: 1.6rem;
                        @media (max-width: $sm) {
                            margin-bottom: .8rem;
                        }
                    }
                    &--theme {
                        &-light-dark {
                            background: linear-gradient(270deg, rgba(24,24,41, 0) 0%, rgba(24,24,41, .40) 100%);
                            color: $color-white-1;
                        }
                        &-light-blue {
                            background: rgba($color-blue-5, .12);
                            color: $color-white-1;
                        }
                    }
                    &:last-child {
                        @media (max-width: $xs) {
                            flex-wrap: wrap;
                        }
                        .main__result-footer-price-card-suptitle {
                            @media (max-width: $sm) {
                                margin-bottom: 0;
                            }
                        }
                    }
                    &-content {
                        margin-right: 2.7rem;
                        display: flex;
                        justify-content: space-between;
                        flex-direction: column;
                        @media (max-width: $xs) {
                            margin-right: 1.2rem;
                        }
                    }
                    &:last-child &-content {
                        @media (max-width: $xs) {
                            width: 100%;
                            margin: 0 0 1.6rem;
                            display: flex;
                            justify-content: space-between;
                            flex-direction: row;
                            align-items: center;
                        }
                    }
                    &-discount {
                        background: $color-orange-1;
                        margin: -1.6rem -1.6rem -1.6rem 0;
                        padding: 1.6rem;
                        flex-shrink: 0;
                        color: $color-white-1;
                        border-radius: 1.6rem;
                        @media (max-width: $xs) {
                            margin: -1.4rem -1.4rem -1.4rem 0;
                        }
                    }
                    &-suptitle {
                        margin: 0 0 .5rem;
                        display: flex;
                        align-items: center;
                        font-weight: 600;
                        font-size: 1.3rem;
                        color: #D0D2DE;
                        img {
                            width: 2rem;
                            height: 2rem;
                            margin-right: .8rem;
                            flex-shrink: 0;
                        }
                    }
                    &-title {
                        font-size: 2.4rem;
                        @media (max-width: $xs) {
                            font-size: 2rem;
                        }
                    }
                    &--theme-light-blue &-title {
                        font-size: 2.4rem;
                    }
                    &-btn {
                        min-height: 5.6rem;
                        padding: 1.6rem 2rem;
                        font-weight: 600;
                        font-size: 1.6rem;
                        border-radius: 1.2rem;
                        @media (max-width: $xs) {
                            width: 54%;
                            max-width: 17rem;
                            min-height: 5.4rem;
                            padding: 1.4rem .6rem;
                        }
                    }
                    &:last-child &-btn {
                        @media (max-width: $xs) {
                            width: 100%;
                            max-width: 100%;
                        }
                    }
                }
            }
        }
        &-copyright {
            margin-top: 3.2rem;
            padding-top: 3.2rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-top: 1px solid rgba($color-white-1, .16);
            @media (max-width: $md) {
                flex-direction: column;
                align-items: center;
            }
            &-guard {
                background: linear-gradient(90deg, rgba(55, 198, 171, 0.12) 0%, rgba(55, 198, 171, 0.00) 100%);
                padding: .8rem 1.2rem;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 1.4rem;
                color: #37C6AB;
                border-radius: 1.2rem;
                @media (max-width: $md) {
                    margin-bottom: 2.4rem;
                }
                @media (max-width: $xs) {
                    width: 100%;
                }
                img {
                    width: 2.4rem;
                    height: 2.4rem;
                    margin-right: .8rem;
                    display: block;
                }
            }
            &-logo {
                display: block;
                margin: 0;
            }
        }
    }
    &__transfer-wrap {
        display: none;
    }
    &__content {
        flex-grow: 1;
        display: none;
        &.is-show {
        }
        &-faq {
            background: rgba(44,50,62, .88);
            backdrop-filter: blur(4px);
            color: $color-white-1;
            border-radius: 1.6rem;
            &-header {
                background: rgba(255,255,255, .04);
                padding: 3.2rem;
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-radius: 1.6rem 1.6rem 0 0;
                position: relative;
                z-index: 1;
                overflow: hidden;
                @media (max-width: $sm) {
                    flex-wrap: wrap;
                }
                &:before {
                    background: url(../images/faq-decor.svg) 50% no-repeat;
                    background-size: contain;
                    width: 20rem;
                    height: 20rem;
                    content: '';
                    position: absolute;
                    top: -4rem;
                    right: -4rem;
                    z-index: -1;
                    @media (max-width: $sm) {
                        margin-left: -10rem;
                        right: initial;
                        left: 50%;
                    }
                }
                &-toggle {
                    @extend .btn-reset, .area-click;
                    font-weight: 600;
                    font-size: 1.2rem;
                    color: rgba($color-white-1, .6);
                    border-bottom: 1px dashed currentColor;
                    position: absolute;
                    top: 3.2rem;
                    left: 3.2rem;
                    transition: all .3s;
                    transition-property: color;
                    cursor: pointer;
                    @media (max-width: $sm) {
                        min-width: 54px;
                        margin-left: -27px;
                        top: 1.6rem;
                        left: 50%;
                    }
                    &:hover {
                        @media (min-width: $md-mf) {
                            color: $color-white-1;
                        }
                    }
                }
                &-content {
                    @extend .title-size-2;
                    max-width: 50rem;
                    padding: 3rem 5.6rem 3rem 0;
                    @media (max-width: $sm) {
                        max-width: 100%;
                        padding: 0;
                        text-align: center;
                    }
                }
                &-picture {
                    max-width: 14rem;
                    margin-left: auto;
                    flex-shrink: 0;
                    @media (max-width: $sm) {
                        margin: .8rem auto 1.8rem auto;
                        order: -1;
                    }
                    img {
                        width: 100%;
                        height: auto;
                        display: block;
                    }
                }
            }
            &-list {
                padding-bottom: 3.2rem;
                &-item {
                    border-top: 1px solid rgba($color-white-1, .1);
                    &-toggle {
                        @extend .btn-reset;
                        width: 100%;
                        padding: 1.6rem 3.2rem;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        font-size: 1.5rem;
                        color: $color-white-1;
                        text-align: left;
                        transition: all .4s;
                        cursor: pointer;
                        @media (max-width: $sm) {
                            padding-right: 2.4rem;
                            padding-left: 2.4rem;
                        }
                        &:hover {
                            @media (min-width: $md-mf) {
                                background: rgba($color-black-1, .1);
                            }
                        }
                        &-title {
                        }
                        &-icon {
                            max-width: 2.4rem;
                            height: 2.4rem;
                            flex: 0 0 2.4rem;
                            color: $color-gray-2;
                            transition: all .4s;
                        }
                    }
                    &.is-show &-toggle-icon {
                        color: $color-white-1;
                        transform: scaleY(-1);
                    }
                    &-content {
                        background: $color-blue-1;
                        margin: 0 3.2rem 1.6rem;
                        padding: 1.2rem 1.6rem;
                        font-weight: 600;
                        font-size: 1.3rem;
                        line-height: 1.32;
                        border-radius: .2rem 1.2rem 1.2rem 1.2rem;
                        @media (max-width: $sm) {
                            margin-right: 2.4rem;
                            margin-left: 2.4rem;
                        }
                    }
                    &:not(.is-show) &-content {
                        display: none;
                    }
                }
            }
        }
    }
}

body.is-page-check .main__inner,
body.is-page-check .main__header {
    max-width: calc(32rem + 2 * var(--gap-fluid));
}

.main__header {
    transition: all .4s;
}

body.is-page-check .main__header-content {
    display: none;
}

body.is-page-check .main__aside {
    margin-right: 0;
}

body.is-animation .main__aside {
    transition-delay: 0s;
}

.bg-el {
    width: 118rem;
    height: 100%;
    margin: 0 auto;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transform: translateX(32rem);
    transition: all .8s;
    z-index: -1;
    @media (max-width: $sm) {
        display: none;
    }
    &__item {
        width: 8.2rem;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        transition: all .15s;
        &:before {
            background: url(../images/bg-hook.svg) center 0 no-repeat;
            background-size: contain;
            width: 100%;
            height: 100%;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            transition: all 1s;
            transition-timing-function: cubic-bezier(.66,1.1,.37,1.02);
            opacity: .5;
            z-index: -1;
        }
        &--1,
        &--2,
        &--3,
        &--4 {
        }
        &--1 {
            margin-right: 88.0rem;
            &:before {
                transition-delay: .5s;
            }
        }
        &--2 {
            margin-right: 61.0rem;
            top: -28.0rem;
            &:before {
                transition-delay: 1.8s;
            }
        }
        &--3 {
            margin-right: 29.0rem;
            top: -3rem;
            &:before {
                transition-delay: 2.6s;
            }
        }
        &--4 {
            margin-right: -4rem;
            top: -14rem;
            &:before {
                transition-delay: 3.6s;
            }
        }
        &-img {
            background: 50% no-repeat;
            background-size: contain;
            position: absolute;
            top: 0;
            left: 0;
            transition: all 1s;
            transition-timing-function: cubic-bezier(.66,1.1,.37,1.02);
            z-index: -2;
            &--1 {
                background-image: url(../images/bg-el-1.png);
                width: 18rem;
                height: 18rem;
                margin: 50rem 0 0 -4.8rem;
                transition-delay: .5s;
            }
            &--2 {
                background-image: url(../images/bg-el-2.png);
                width: 20rem;
                height: 20rem;
                margin: 49.6rem 0 0 -4.4rem;
                transition-delay: 1.8s;
            }
            &--3 {
                background-image: url(../images/bg-el-3.png);
                width: 22rem;
                height: 22rem;
                margin: 48.3rem 0 0 -7rem;
                transition-delay: 2.6s;
            }
            &--4 {
                background-image: url(../images/bg-el-4.png);
                width: 16.2rem;
                height: 16.2rem;
                margin: 50.4rem 0 0 -4rem;
                transition-delay: 3.6s;
            }
        }
    }
}

body.is-page-check .bg-el {
    filter: blur(12px);
    transform: translate(-7rem, 18rem) scale(1.66);
}

body.is-page-check .main__attention {
    opacity: 1;
    visibility: visible;
}

body:not(.is-animation-el) {
    .bg-el__item {
        &--1 {
            &:before {
                transform: translateY(-100vh);
                transition-delay: .1s;
            }
        }
        &--2 {
            &:before {
                transform: translateY(-100vh);
                transition-delay: .8s;
            }
        }
        &--3 {
            &:before {
                transform: translateY(-100vh);
                transition-delay: 1.1s;
            }
        }
        &--4 {
            &:before {
                transform: translateY(-100vh);
                transition-delay: 1.5s;
            }
        }
        &-img {
            &--1 {
                transform: translateY(100vh);
                transition-delay: .1s;
            }
            &--2 {
                transform: translateY(100vh);
                transition-delay: .8s;
            }
            &--3 {
                transform: translateY(100vh);
                transition-delay: 1.1s;
            }
            &--4 {
                transform: translateY(100vh);
                transition-delay: 1.5s;
            }
        }
    }
}