// typography
$ff-default: 'Inter', Arial, sans-serif;
$ff-work-sans: 'Work Sans', Arial, sans-serif;
$ff-space-mono: 'Space Mono', Arial, sans-serif;

// pallets
$color-black-1: #000;
$color-black-2: #181829;
$color-black-3: #2C323E;
$color-white-1: #fff;
$color-white-2: #F6F8FA;
$color-gray-1: #F6F6F6;
$color-gray-2: #6C6F71;
$color-blue-1: #2959BE;
$color-blue-2: #4F3CC9;
$color-blue-3: #3C8EC9;
$color-blue-4: #00579F;
$color-blue-5: #246BFD;
$color-yellow-1: #FFB11B;
$color-yellow-2: #F7931A;
$color-red-1: #C93CA2;
$color-red-2: #C95E3C;
$color-green-1: #369C33;
$color-green-2: #53BA76;
$color-green-3: #15995A;
$color-green-4: #139F4B;
$color-brown-1: #7C4D00;
$color-brown-2: #784F00;
$color-brown-3: #FFA02E;
$color-orange-1: #FF472E;

// gradients
$gradient-blue-1: linear-gradient(287.56deg, $color-blue-2 0%, $color-blue-3 100%);
$gradient-red-1: linear-gradient(287.56deg, rgba($color-red-1, .88) 0%, rgba($color-red-2, .88) 100%);
$gradient-green-1: linear-gradient(287.56deg, rgba($color-green-1, .88) 0%, rgba($color-green-2, .88) 100%);

// breakpoints
$xl: 1440px;
$xl-mf: $xl + 1;

$lg: 1199px;
$lg-mf: $lg + 1;

$md: 991px;
$md-mf: $md + 1;

$sm: 767px;
$sm-mf: $sm + 1;

$xs: 576px;
$xs-mf: $xs;